.header {
  position: relative;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1660px;
    margin: 0 auto;
    padding: 25px 30px;
    @include r(1200) {
      padding: 22px 30px; }
    @include r(1023) {
      padding: 15px 15px; } }
  &__actions {
    display: flex;
    align-items: center;
    padding-top: 4px;
    @include r(767) {
      padding-top: 3px; }
    @include r(500) {
      padding-top: 1px; } }
  &__links {
    display: flex;
    @include r(767) {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      padding: 0 15px;
      justify-content: space-between;
      .sign-in {
        margin-right: 0; }
      .header-link {
        margin-right: 0; } } } }

.logo {
  display: block;
  max-width: 285px;
  @include r(1360) {
    max-width: 250px; }
  @include r(767) {
    max-width: 200px; }
  @include r(500) {
    max-width: 150px; }
  &__pic {
    max-width: 100%;
    display: block; } }

.sign-in {
  text-decoration: none;
  margin-right: 95px;
  font-family: 'Oxygen', sans-serif;
  font-weight: 700;
  font-size: 0;
  color: $brand-dark-blue;
  transition: color 0.15s linear;
  @include r(1360) {
    margin-right: 70px; }
  @include r(767) {
    margin-right: 30px; }
  @include r(500) {
    margin-right: 0; }
  &:hover {
    color: lighten($brand-dark-blue, 15%);
    .icon {
      fill: lighten($brand-dark-blue, 15%); } }
  .icon {
    vertical-align: middle;
    font-size: rem(40);
    transition: fill 0.15s linear;
    @include r(767) {
      font-size: rem(35); }
    @include r(500) {
      font-size: rem(30); } }
  span {
    margin-left: 12px;
    display: inline-block;
    vertical-align: middle;
    font-size: rem(19);
    @include r(767) {
      margin: 1px 0 0 10px;
      font-size: rem(18); }
    @include r(500) {
      font-size: rem(17); } } }

.header-link {
  display: inline-block;
  margin-right: 50px;
  font-family: 'Oxygen', sans-serif;
  font-size: rem(19);
  font-weight: 700;
  text-decoration: none;
  color: $green;
  transition: color 0.15s linear;
  span {
    position: relative;
    padding: 9px 0;
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $green;
      transition: width 0.2s ease; } }
  &:hover {
    color: lighten($green, 10%);
    span:after {
      width: 0; } }
  @include r(767) {
    font-size: rem(18); }
  @include r(500) {
    font-size: rem(17); }
  .icon {
    display: none;
    margin-right: 4px;
    font-size: rem(15);
    @include r(767) {
      display: inline-block; } } }

.theme-switcher {
  position: relative;
  font-size: 0;
  &:after {
    content: '';
    position: absolute;
    width: 1px;
    background: #000;
    top: 0;
    left: 50px;
    bottom: 0; }
  &__button {
    background: none;
    vertical-align: middle;
    svg {
      display: block;
      font-size: rem(34); }
    &--light {
      margin-right: rem(16);
      svg {
        fill: #FDAC34; } }
    &--dark {
      margin-left: rem(22.4);
      svg {
        font-size: rem(24); } } } }

body:not(.body--article) {
  .theme-switcher {
    @include r(500) {
      position: absolute;
      z-index: 1;
      top: 100%;
      right: 12px;
      margin-top: 10px;
      padding: 6px 12px 6px 9px;
      box-shadow: 0 4px 15px 0 rgba(0,0,0, 0.2);
      border-radius: 5px;
      background: #fff;
      &:after {
        top: 5px;
        left: 54px;
        bottom: 5px; } } }
  .theme-switcher__button {
    &--light {
      @include r(500) {
        margin-right: rem(11.2); } }
    &--dark {
      @include r(500) {
        margin-left: rem(16); } } } }
